import { useEffect, useState } from "react"
import { getCookie, deleteCookies, setCookie } from "../cookie/cookieManager"

import { useNavigate } from "react-router-dom";

import supabase from "../config/supabaseClient";

function Profile() {
    const navigate = useNavigate();

    const [profileSidePanel, setProfileSidePanel] = useState('false')

    if (profileSidePanel === 'false') {
        return (
            <button className='ProfileHeaderBtn' onClick={() => {setProfileSidePanel('true')}}>
                <img src={getCookie('profilePhoto')} width='60px'/>
            </button>
        )
    } 
    if (profileSidePanel === 'true') {
        return (
            <ProfileDisplay setProfileSidePanel={setProfileSidePanel}/>
        )
    }

    if (profileSidePanel === 'editProfilePanel') {
        return (
            <EditProfileDisplay setProfileSidePanel={setProfileSidePanel}/>
        )
    }
}

function ProfileDisplay({setProfileSidePanel}) {
    const navigate = useNavigate();

    console.log('hi')

    return (
        <div>
            <button onClick={() => {setProfileSidePanel('true')}}>
                <img src={getCookie('profilePhoto')} width='60px'/>
            </button>

            <div className="ProfileSidePanelBg" onClick={() => {setProfileSidePanel('false')}}>
            </div>

            <div className="ProfileSidePanel">
                <div className="PS_UserDetails">
                        <div className="PSUD_ImgContainer">
                            <img src={getCookie('profilePhoto')}/>
                        </div>
                        <div className="PSUD_UsenameContainer">
                            {getCookie('username')}
                        </div>
                        <div className="PSUD_FollowerContainer">
                            <div className="PSUD_Followes">
                                Follower: 0
                            </div>
                            <div className="PSUD_Following">
                                Following: 0
                            </div>
                        </div>
                        <div className="PSUD_EditProfileConainerBtn">
                            <div onClick={() => {setProfileSidePanel('editProfilePanel')}}>
                                <img src="img/EditIcon.svg"/>
                                Edit Profile
                            </div>
                        </div>
                </div>
                <div className="PS_ButtonContainer">
                    <div className="PSBC_Button">
                        <div>
                            <img src="img/MessageIcon.svg"/>
                            Message
                        </div>
                        <img src="img/RightArrowIcon.svg"/>
                    </div>
                    <hr/>
                    <div className="PSBC_Button">
                        <div>
                            <img src="img/SettingsIcon.svg"/>
                            Settings
                        </div>
                        <img src="img/RightArrowIcon.svg"/>
                    </div>
                    <hr/>
                    <div className="PSBC_Button">
                        <div>
                            <img src="img/LogoutIcon.svg"/>
                            Log Out
                        </div>
                        <img src="img/RightArrowIcon.svg" onClick={() => {
                        deleteCookies()
                        navigate('/')
                        }}/>
                    </div>
                    <hr/>
                    <div className="PSBC_Button">
                        <div>
                            <img src="img/BackIcon.svg"/>
                            Back
                        </div>
                        <img src="img/RightArrowIcon.svg" onClick={() => {setProfileSidePanel('false')}}/>
                    </div>
                </div>
            </div>
        </div>
    )
}

function EditProfileDisplay ({setProfileSidePanel}) {
    var tempArr = new Array()
    const navigate = useNavigate();
    const [defaultProfileIcons, setDefaultProfileIcons] = useState([])
    const [profilePhoto, setProfilePhoto] = useState(getCookie('profilePhoto'))
    const [username, setUsername] = useState(getCookie('username'))
    const [userCustomProfilePhoto, setUserCustomProfilePhoto] = useState('')

    const storage = supabase.storage.from('images')

    for(let i=0; i<3; i++) {
        tempArr[i] = storage.getPublicUrl(`images/profileIcons/defaultIcons/ProfileIcon${i+1}.svg`) 
    }

    useEffect(() => {
        setDefaultProfileIcons(tempArr)
    }, [])

    var displayDefaultProfileIcons = defaultProfileIcons.map(function(item, i) {
        return (
            <img key={i} onClick={() => {setProfilePhoto(item.data.publicUrl)}} src={item.data.publicUrl} />
        )
    })

    async function updateProfilePhotoDatabase() {
        const {data, error} = await supabase.storage.from('images').list('images/profileIcons')

        if (data) {
            //console.log(data)
            addImageToDatabase(data)
        }

        if (error) {
            console.log(error)
        }

        async function addImg(path) {
            console.log('hi')
            const {imgData, imgError} = await supabase.storage
            .from('images')
            .upload(`images/profileIcons/${getCookie('userId')}`, path)
            
            if (imgData) {
                console.log(imgData)
            }

            if (imgError) {
                console.log(imgError)
            }
        }

        async function deleteImg() {
            console.log('hello')
            const {imgData, imgError} = await supabase.storage
            .from('images')
            .remove([`images/profileIcons/${getCookie('userId')}`])

            if (imgData) {
                console.log(imgData)
            }

            if (imgError) {
                console.log(imgError)
            }
        }

        async function addImageToDatabase(data) {
            for(var i=0; i<data.length; i++) {
                if (data[i].name === getCookie('userId')) {
                    deleteImg()
                    addImg(userCustomProfilePhoto)
                } else {
                    addImg(userCustomProfilePhoto)
                }
            }
        }
    }

    async function updateDatabase() {
        updateProfilePhotoDatabase()
        const storage = supabase.storage.from('images')
        var profilePhotoUrl = storage.getPublicUrl(`images/profileIcons/${getCookie('userId')}`) 
        console.log(profilePhotoUrl.data.publicUrl)

        setProfilePhoto(profilePhotoUrl.data.publicUrl)

        const {data, error} = await supabase
        .from('User')
        .update({profilePhoto:  profilePhotoUrl.data.publicUrl, username: username})
        .eq('id', getCookie('userId'))

        if (data) {
            console.log(data)
        }

        if (error) {
            console.log(error)
        }

        const cookieData = {
            id: getCookie('userId'),
            username: username,
            userPrivilege: getCookie('userPrivilege'),
            profilePhoto: profilePhotoUrl.data.publicUrl
        }
        
        setCookie('true', cookieData)
        setProfileSidePanel('true')
    }

    return (
        <div>
            <button>
                Profile
            </button>

            <div className="ProfileSidePanelBg" onClick={() => {setProfileSidePanel('false')}}>
            </div>

            <div className="EditProfileSidePanel">
                <div className="EPS_UserDetails">
                    <div className="EPSUD_ImgContainer">
                        <div>
                            <img src={profilePhoto}/>
                            {/* <input className="DHSNAICIS_BaseImg" type="file" onChange={(e) => {AddImage(e.target.files[0])}}/> */}
                            {/* <input type="file" onChange={(e) => {addImg(URL.createObjectURL(e.target.files[0]))}}/> */}
                            <input type="file" onChange={(e) => {setUserCustomProfilePhoto(e.target.files[0])}}/>
                        </div>
                    </div>
                    <div className="EPSUDIC_DefaultContainer">
                            {/* {displayDefaultProfileIcons} */}
                    </div>
                    <div className="EPSUD_UsenameContainer">
                        <textarea rows={1} defaultValue={username} onChange={(e) => {setUsername(e.target.value)}}/>
                    </div>
                </div>

                <div className="EPS_ButtonContainer">
                    <div onClick={() => {setProfileSidePanel('true')}}>
                        Cancel    
                    </div>

                    <div onClick={() => {updateDatabase()}}>
                        Save
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Profile