import { Route, Routes } from "react-router-dom"
import supabase from "../config/supabaseClient"

import { useState, useEffect } from "react"

function HomePages() {
    const [recipes, setRecipes] = useState()

    async function getRecipes() {
        const {data, error} = await supabase
        .from('recipe')
        .select('*')

        if (data) {
            setRecipes(data)
        }

        if (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        getRecipes()
    }, [])

    function getImg(userId) {
        const storage = supabase.storage.from('images')
        const getPublicUrl = storage.getPublicUrl(`images/recipeImg/${userId}`)
        return getPublicUrl.data.publicUrl
    }

    if (recipes) {
        let recipeList = recipes.map(function(item, i) {
            item.img = getImg(item.id)
            return (
                <Routes key={i}>
                    <Route path={`home/${item.name}`} element={<ShowPage recipeDetails={item}/>}/>
                </Routes>
            )
        })
        return recipeList
    }
}

export function ReviewPages() {
    const [recipes, setRecipes] = useState()

    async function getRecipes() {
        const {data, error} = await supabase
        .from('recipeForReview')
        .select('*')

        if (data) {
            setRecipes(data)
        }

        if (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        getRecipes()
    }, [])

    function getImg(userId) {
        const storage = supabase.storage.from('images')
        const getPublicUrl = storage.getPublicUrl(`images/recipeForReviewImg/${userId}`)
        return getPublicUrl.data.publicUrl
    }

    if (recipes) {
        let recipeList = recipes.map(function(item, i) {
            item.img = getImg(item.id)
            return (
                <Routes key={i}>
                    <Route path={`review/${item.name}`} element={<ShowPage recipeDetails={item}/>}/>
                </Routes>
            )
        })
        return recipeList
    }
}


function ShowPage({recipeDetails}) {
    var veg

    if(recipeDetails.veg === true) {
        veg = 'Vegetarian'
    } else {
        veg = 'Non-Vegetarian'
    }

    var time = recipeDetails.time
    var servings = recipeDetails.servings
    
    var calories = recipeDetails.calories
    var carbs = recipeDetails.carbs
    var protein = recipeDetails.protein
    var fat = recipeDetails.fat

    var ingredientsArr = recipeDetails.ingredients.split(',')
    var ingredients = ingredientsArr.map(function(item, i) {
        var itemName = ''
        var itemQuantity = ''
        for(let i=0; i<item.length; i++) {
            if(i>4 && i<(item.indexOf('quantity=')-1)) {
                itemName += item[i]
            }

            if(i>(item.indexOf('quantity=')+8)) {
                itemQuantity += item[i] 
            }   
        }

        return (
            <div key={i} >
                <hr/>
                <div className="RCDRIngCI_Div">
                    <div className="RCDRIngCI_Num">
                        <div>{i+1}</div>
                    </div>
                    <div className="RCDRIngCI_Text">
                        <div className="RCDRIngCIT_Name">{itemName}</div>
                        <div className="RCDRIngCIT_Quantity">{itemQuantity}</div>
                    </div>
                </div>
            </div>
        )
    })

    var instructionsArr = recipeDetails.instructions.split(',instructions=')
    if(instructionsArr[0].includes('instructions=')) {
        var tempString = instructionsArr[0]
        var tempArr = tempString.split(' ')
        tempArr[0] = tempArr[0].split('=')[1]
        
        tempString = tempArr[0]
        for(let i=1; i<tempArr.length; i++) {
            tempString = tempString + ` ${tempArr[i]}`
        }
        instructionsArr[0] = tempString
    }

    var instructions = instructionsArr.map(function(item, i) {
        return (
            <div key={i}>
                <hr/>
                <div className="RCDRInsCI_Div">
                    <div className="RCDRInsCI_Num">
                        <div>{i+1}</div>
                    </div>
                    <div className="RCDRInsCI_Text">
                        {item}
                    </div>
                </div>
            </div>
        )
    }) 

    return (
        <div className="RC_DisplayRecipe">  
            <div className="RCDR_ImgAndDescription">
                <div className="RCDR_LeftSide">
                    <div className="RCDR_ImgContainer">
                        <img src={recipeDetails.img}/>
                    </div>
                </div>

                <div className="RCDR_RightSide">
                    <div className="RCDR_DescriptionContainer">
                        <div className="RCDRDC_Name">{recipeDetails.name}</div>
                        <div className="RCDRDC_Description">{recipeDetails.description}</div>
                    </div>

                    <div className="RCDR_RecipeDetails">
                            <div className="RCDR_VegOrNonVeg">{veg}</div>
                            <div className="RCDR_Time">{time} min</div>
                            <div className="RCDR_Servings">{servings} servings</div>
                        </div>

                    <div className="RCDR_NutrientsContainer">
                        <div className="RCDRDCNC_Calories">
                            <div className="RCDRDCNCC_Title">Calories</div>
                            <div>{calories} cal</div>
                        </div>
                        <div className="RCDRDC_VR"/>
                        <div className="RCDRDCNC_Carbs">
                            <div className="RCDRDCNCC_Title">Carbs</div>
                            <div>{carbs} g</div>
                        </div>
                        <div className="RCDRDC_VR"/>
                        <div className="RCDRDCNC_Protein">
                            <div className="RCDRDCNCC_Title">Protein</div>
                            <div>{protein} g</div>
                        </div>
                        <div className="RCDRDC_VR"/>
                        <div className="RCDRDCNC_Fat">
                            <div className="RCDRDCNCC_Title">Fat</div>
                            <div>{fat} g</div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="RCDR_IngredientsAndInstructionsContainer">
                <div className="RCDR_InstructionsContainer">
                    <div className="RCDRInsC_Title">Instructions</div>
                    <div className="RCDRInsC_Instructions">{instructions}</div>
                </div>

                <div className="RCDR_IngredientsContainer">
                    <div className="RCDRIngC_Title">Ingredients</div>
                    <div className="RCDRInsC_Ingredients">{ingredients}</div>
                </div>
            </div>
        </div>
    )
}

export default HomePages