import { useNavigate } from 'react-router-dom';
import { getCookie } from './cookie/cookieManager';
import Profile from './pages/profile';
import supabase from './config/supabaseClient';
import { useEffect, useState } from 'react';
import { wait } from '@testing-library/user-event/dist/utils';

function NavBarProfileComponent() {
    const navigate = useNavigate()
    if (getCookie('signInStatus') == 'true') {
        return (
            <Profile/>
        )
    } else {
        return (
            <div className='NavBarRegisterContainer'>
                <button className='NBRC_LogInBtn' onClick={()=>{navigate('/signin')}}>
                    Log In
                </button>
                <button className='NBRC_SignUpBtn' onClick={()=>{navigate('/signup')}}>
                    Sign Up
                </button>
            </div>
        )
    }
}

function NavBarSearchResult({SearchBarFocus, SearchBarResultArray, setSearchBarFocus}) {
    const navigate = useNavigate()

    if (SearchBarFocus && SearchBarResultArray) {
        var temp = SearchBarResultArray.map(function(item, i) {
            return (
                <div key={i} onClick={() => {
                    navigate(`home/${item.name}`)
                    setSearchBarFocus(false)
                    }}>
                    {item.name}
                </div>
            )
        })   

        return (
            <div>
                <div className='NBCSBC_ResultArray'>
                    {temp}
                </div>
                <div className='NBCSB_Bg' onClick={() => {setSearchBarFocus(false)}}/>
            </div>
        )
    }
}

function NavBar({signInStatus, recipes}) {
    const navigate = useNavigate()

    const [SearchBarFocus, setSearchBarFocus] = useState(false)
    const [SearchBarText, setSearchBarText] = useState('')
    const [SearchBarResultArray, setSearchBarResultArray] = useState([])
    
    function handleInput(input) {
        var result = new Array()

        if (recipes) {
            for(let i=0; i<recipes.length; i++) {
                if (recipes[i].name.toLowerCase().includes(input.toLowerCase())) {
                    result.push(recipes[i])
                }
            } 
            setSearchBarResultArray(result)
        }
    }

    return (
        <div className="NavBar">
            <div className="NavBarLogo" onClick={()=>{navigate('/home')}}>
                Eatery
            </div>
            <div className='NavBarComponents'>
                <div className='NBC_CreateBtn' onClick={() => {navigate('/create')}}>Create</div>
                <div className='NBC_SearchBarContainer'>
                    <textarea rows={1} className='NBC_SearchBar' placeholder='Search' onClick={() => {setSearchBarFocus(true)}} onChange={(e) => {handleInput(e.target.value)}}/>
                    <NavBarSearchResult SearchBarFocus={SearchBarFocus} SearchBarResultArray={SearchBarResultArray} setSearchBarFocus={setSearchBarFocus}/>
                </div>
                <NavBarProfileComponent/>
            </div>
        </div>
    )
}

export default NavBar