import { useState } from "react"
import CreateRecipe from "./createRecipe"
import MyRecipes from "./myRecipes"
import { deleteCookies } from "../../cookie/cookieManager"
import { useNavigate } from "react-router-dom"

function DashboardHeroSection({sectionName}) {
    if (sectionName === 'createRecipe') {
        return (
            <CreateRecipe/>
        )
    } 

    if (sectionName === 'myRecipes') {
        return (
            <MyRecipes/>
        )
    }
}

function Dashboard() {
    const [dashboardHeroSection, setDashboardHeroSection] = useState('myRecipes')
    const navigate = useNavigate()

    function buttonHandler(div, prop) {
        setDashboardHeroSection(prop)
        
        if(div.target.className !== 'dashboardHeaderSelectedBtn') {
            var prevDiv = document.getElementsByClassName('dashboardHeaderSelectedBtn')
            prevDiv[0].classList.add('dashboardHeaderNotSelectedBtn')
            prevDiv[0].classList.remove('dashboardHeaderSelectedBtn')
            div.target.classList.add('dashboardHeaderSelectedBtn')
            div.target.classList.remove('dashboardHeaderNotSelectedBtn')
        }
    }

    function HandleLogOut() {
        deleteCookies()
        navigate('/')
    }
    
    return (
        <div className="dashboardContainer">
            <div className="dashboardHeader">
                <div>
                    <button className='dashboardHeaderSelectedBtn' onClick={(e) => {buttonHandler(e, 'myRecipes')}}>
                        My Recipe
                    </button>
                    <button className='dashboardHeaderNotSelectedBtn' onClick={(e) => {buttonHandler(e, 'createRecipe')}}>
                        Create Recipe
                    </button>
                </div>
                <div>
                    <button className='logOutBtn' onClick={HandleLogOut}>Log Out</button>
                </div>
            </div>
            <div className="dashboardHerosection">
                <DashboardHeroSection sectionName={dashboardHeroSection}/>
            </div>
        </div>
    )
}

export default Dashboard