import { useEffect, useState } from "react"
import supabase from "../../config/supabaseClient"
import { getCookie } from "../../cookie/cookieManager"

function MyRecipes() {
    const [heroSection, setheroSection] = useState('DisplayArray')

    return (
        <div className="RecipeContainer">
            <HeroSectionDiv heroSection={heroSection} setHeroSection={setheroSection}/> 
        </div>
    )
}

function HeroSectionDiv({heroSection, setHeroSection}) {
    if (heroSection === 'DisplayArray') {
        return (
            <DisplayArray setHeroSection={setHeroSection}/>
        )
    }

    if (heroSection.type === 'DisplayReviewRecipe') {
        return (
            <DisplayRecipe setHeroSection={setHeroSection} recipeDetails={heroSection}/>
        )
    }
}

function DisplayRecipe({setHeroSection, recipeDetails}) {
    var veg

    if(recipeDetails.veg === true) {
        veg = 'Vegetarian'
    } else {
        veg = 'Non-Vegetarian'
    }

    var ingredientsArr = recipeDetails.ingredients.split(',')
    var ingredients = ingredientsArr.map(function(item, i) {
        var itemName = ''
        var itemQuantity = ''
        for(let i=0; i<item.length; i++) {
            if(i>4 && i<(item.indexOf('quantity=')-1)) {
                //console.log(item[i])
                itemName += item[i]
            }

            if(i>(item.indexOf('quantity=')+8)) {
                itemQuantity += item[i] 
            }   
        }

        return (
            <div key={i} className="RRCDRIngCI_Div">
                <div className="RCDRIngCI_Num">
                    <div>{i+1}</div>
                </div>
                <div className="RCDRIngCI_Text">
                    <div>{itemName}</div>
                    <div>{itemQuantity}</div>
                </div>
            </div>
        )
    })

    var instructionsArr = recipeDetails.instructions.split(',instructions=')
    if(instructionsArr[0].includes('instructions=')) {
        var tempString = instructionsArr[0]
        var tempArr = tempString.split(' ')
        tempArr[0] = tempArr[0].split('=')[1]
        
        tempString = tempArr[0]
        for(let i=1; i<tempArr.length; i++) {
            tempString = tempString + ` ${tempArr[i]}`
        }
        instructionsArr[0] = tempString
    }

    var instructions = instructionsArr.map(function(item, i) {
        return (
            <div key={i} className="RCDRInsCI_Div">
                <div className="RCDRInsCI_Num">
                    <div>{i+1}</div>
                </div>
                <div className="RCDRInsCI_Text">
                    {item}
                </div>
            </div>
        )
    }) 

    return (
        <div className="RC_DisplayRecipe">
            <div className="RCDR_ButtonContainer">
                <button onClick={() => {setHeroSection('DisplayArray')}}>Back</button>
            </div>
            <div className="RCDR_ImgAndDescription">
                <div className="RCDR_ImgContainer">
                    <img src={recipeDetails.img}/>
                </div>
                <div className="RCDR_DescriptionContainer">
                    <div className="RCDRDC_Name">{recipeDetails.name}</div>
                    <div className="RCDRDC_Description">{recipeDetails.description}</div>
                    <div className="RCDRDC_VegOrNonVeg">{veg}</div>
                </div>
            </div>
            <div className="RCDR_IngredientsAndInstructionsContainer">
                <div className="RCDR_IngredientsContainer">
                    <div className="RCDRIngC_Title">Ingredients</div>
                    <div className="RCDRInsC_Ingredients">{ingredients}</div>
                </div>

                <div className="RCDR_InstructionsContainer">
                    <div className="RCDRInsC_Title">Instructions</div>
                    <div className="RCDRInsC_Instructions">{instructions}</div>
                </div>
            </div>
        </div>
    )
}

function DisplayArray({setHeroSection}) {
    console.log()

    const [dataComponent, setDataComponent] = useState([])

    async function checkForRecipes() {
        const {data, error} = await supabase
        .from('recipe')
        .select('*')

        if(data) {
            var tempArr = []
            for(let i=0; i<data.length; i++) {
                if(data[i].parentId === getCookie('userId')) {
                    tempArr.push(data[i])
                }
            }
            setDataComponent(tempArr)
        }

        if(error) {
            console.log(error)
        }
    }

    useEffect(() => {
        checkForRecipes()
    }, [])

    function getImg(userId) {
        const storage = supabase.storage.from('images')
        const getPublicUrl = storage.getPublicUrl(`images/recipeImg/${userId}`)
        return getPublicUrl.data.publicUrl
    }

    let showRecipeDiv = dataComponent.map(function(item, i) {
        return (
            <RecipeComponent key={i} setHeroSection={setHeroSection} id={item.id} img={getImg(item.id)} name={item.name} description={item.description} veg={item.veg} ingredients={item.ingredients} instructions={item.instructions}/>
        )
    })

    return (
        <div className="R_Array">
            {showRecipeDiv}
        </div>
    )
}

function RecipeComponent({setHeroSection, id, img, name, description, veg, ingredients, instructions}) {
    let heroSectionComponent = {
        type: 'DisplayReviewRecipe',
        id: id,
        img: img,
        name: name,
        description: description,
        veg: veg,
        ingredients: ingredients,
        instructions: instructions
        
    }

    return (
        <div className="R_element">
            <img src={img}/>
            <div>
                <div>{name}</div>
                <button onClick={() => {setHeroSection(heroSectionComponent)}}>Show Recipe</button>
            </div>
        </div>
    )
}
   
export default MyRecipes