import { useEffect, useState } from "react"
import supabase from "../../config/supabaseClient"
import { useNavigate } from "react-router-dom"

async function AcceptRecipe(recipeDetails) {
    var id = recipeDetails.id
    var parentId = recipeDetails.parentId
    var name = recipeDetails.name
    var description = recipeDetails.description
    var veg = recipeDetails.veg
    var time = recipeDetails.time
    var servings = recipeDetails.servings
    var calories = recipeDetails.calories
    var carbs = recipeDetails.carbs
    var protein = recipeDetails.protein
    var fat = recipeDetails.fat
    var ingredients = recipeDetails.ingredients
    var instructions = recipeDetails.instructions

    const {data, error} = await supabase
    .from('recipe')
    .insert({parentId: parentId, name: name, description: description, veg: veg, time: time, servings: servings, calories: calories, carbs: carbs, protein: protein, fat: fat, ingredients: ingredients, instructions: instructions})
    .select('*')

    if (data) {
        console.log(data)
    }

    if (error) {
        console.log(error)
    }

    const {reviewData, reviewError} = await supabase
    .from('recipeForReview')
    .delete()
    .eq('id', id)

    if (reviewData) {
        console.log(reviewData)
    }

    if (reviewError) {
        console.log(reviewError)
    }

    const storage = supabase.storage.from('images')
    storage.move(`images/recipeForReviewImg/${id}`, `images/recipeImg/${data[0].id}`)

} 

function Review() {
    const [RRC_heroSection, setRRC_heroSection] = useState('DisplayArray')

    return (
        <div className="reviewRecipeContainer">
            <RRC_HeroSectionDiv heroSection={RRC_heroSection} setHeroSection={setRRC_heroSection}/> 
        </div>
    )
}

function RRC_HeroSectionDiv({heroSection, setHeroSection}) {
    if (heroSection === 'DisplayArray') {
        return (
            <DisplayArray setHeroSection={setHeroSection}/>
        )
    }
}


function DisplayArray({setHeroSection}) {
    const [dataComponent, setDataComponent] = useState([])
    const [recipeDivComponent, setRecipeDivComponent] = useState(null)

    async function checkForRecipes() {
        const {data, error} = await supabase
        .from('recipeForReview')
        .select('*')

        if(data) {
            // console.log(data)
            setDataComponent(data)
        }

        if(error) {
            console.log(error)
        }
    }

    useEffect(() => {
        checkForRecipes()
    }, [])

    function getImg(userId) {
        const storage = supabase.storage.from('images')
        const getPublicUrl = storage.getPublicUrl(`images/recipeForReviewImg/${userId}`)
        return getPublicUrl.data.publicUrl
    }

    let showRecipeDiv = dataComponent.map(function(item, i) {
        return (
            <RecipeComponent key={i} setHeroSection={setHeroSection} id={item.id} parentId={parseInt(item.parentId)} img={getImg(item.id)} name={item.name} description={item.description} veg={item.veg} time={item.time} servings={item.servings} calories={item.calories} carbs={item.carbs} protein={item.protein} fat={item.fat} ingredients={item.ingredients} instructions={item.instructions}/>
        )
    })

    return (
        <div className="R_Array">
                {showRecipeDiv}
        </div>
    )
}

function RecipeComponent({setHeroSection, id, parentId, img, name, description, veg, time, servings, calories, carbs, protein, fat, ingredients, instructions}) {
    const navigate = useNavigate()

    let heroSectionComponent = {
        type: 'DisplayReviewRecipe',
        id: id,
        parentId: parentId,
        img: img,
        name: name,
        description: description,
        veg: veg,
        time: time,
        servings: servings,
        calories: calories,
        carbs: carbs,
        protein: protein,
        fat: fat,
        ingredients: ingredients,
        instructions: instructions
        
    }

    return (
        <div className="R_element">
            <img src={img}/>
            <div>
                <div>{name}</div>
                <button onClick={() => {navigate(`/review/${name}`)}}>Show Recipe</button>
            </div>
        </div>
    )
}
   
export default Review