import { useEffect, useState } from "react"
import supabase from "../../config/supabaseClient"
import { useNavigate } from "react-router-dom"
import { getCookie } from "../../cookie/cookieManager"

function IngredientsDiv({serialNum, ingredients, setIngredients, handleDelete}) {
    function addToIngredients(type, value) {
        if(type === 'name') {
            ingredients[serialNum].name = value
        }

        if(type === 'quantity') {
            ingredients[serialNum].quantity = value
        }

        setIngredients(ingredients)
    }

    const [NameRowHeight, setNameRowHeight] = useState(1)
    const [NameRowScrollHeight, setNameRowScrollHeight] = useState(null)

    const [QuantityRowHeight, setQuantityRowHeight] = useState(1)
    const [QuantityRowScrollHeight, setQuantityRowScrollHeight] = useState(null)

    return (
        <div className="CRCIngC_Component">
            <div className="CRCIngCC_SerialNum">{serialNum+1}</div>
            <div className="CRCIngCC_NameAndQuantityContainer">
                <textarea className="CRCIngCC_Name"
                    rows={NameRowHeight} 
                    onClick={(e) => {setNameRowScrollHeight(e.target.scrollHeight)}} 
                    onInput={(e) => {
                    if(e.target.scrollHeight !== NameRowScrollHeight) {
                        setNameRowScrollHeight(e.target.scrollHeight)
                        setNameRowHeight(NameRowHeight+1)
                    }}} placeholder="Name" onChange={(e) => {addToIngredients('name', e.target.value)}}/>
                
                <textarea className="CRCIngCC_Quantity" 
                    rows={QuantityRowHeight} 
                    onClick={(e) => {setQuantityRowScrollHeight(e.target.scrollHeight)}} 
                    onInput={(e) => {
                    if(e.target.scrollHeight !== QuantityRowScrollHeight) {
                        setQuantityRowScrollHeight(e.target.scrollHeight)
                        setQuantityRowHeight(QuantityRowHeight+1)
                    }}} placeholder="Quantity" onChange={(e) => {addToIngredients('quantity', e.target.value)}}/>
            </div>
        </div>
    )
}

function InstructionsDiv({serialNum, instructions, setInstructions}) {
    function addToInstructions(type, value) {
        if(type === 'description') {
            instructions[serialNum].description = value
        }

        setInstructions(instructions)
    }

    const [DescriptionRowHeight, setDescriptionRowHeight] = useState(1)
    const [DescriptionRowScrollHeight, setDescriptionRowScrollHeight] = useState(null)

    return (
        <div className="CRCIC_Component">
            <div className="CRCICC_SerialNum">{serialNum+1}</div>
            <div>
                <textarea className="CRCICC_Description"
                    rows={DescriptionRowHeight} 
                    onClick={(e) => {setDescriptionRowScrollHeight(e.target.scrollHeight)}} 
                    onInput={(e) => {
                    if(e.target.scrollHeight !== DescriptionRowScrollHeight) {
                        setDescriptionRowScrollHeight(e.target.scrollHeight)
                        setDescriptionRowHeight(DescriptionRowHeight+1)
                    }}}placeholder="Description" onChange={(e) => {addToInstructions('description', e.target.value)}}/>
            </div>
        </div>
    )
}

function CreateRecipe() {
    const navigate = useNavigate()
    
    const [img, setImg] = useState({
        path: null,
        url: 'img/imgSelectorBaseImg.svg'
    })
    const [name, setName] = useState('')
    const [description, setDescription] = useState('')
    const [veg, setVeg] = useState('')
    const [time, setTime] = useState('')
    const [servings, setServings] = useState('')
    const [calories, setCalories] = useState('')
    const [carbs, setCarbs] = useState('')
    const [protein, setProtein] = useState('')
    const [fat, setFat] = useState('')
    const [ingredients, setIngredients] = useState([])
    const [instructions, setInstructions] = useState([])

    const imgComponent = {
        path: null,
        url: null
    }

    const ingredientsComponent = {
        name: null,
        quantity: null
    }

    const instructionsComponent = {
        description: null
    }

    function AddImage(path) {
        imgComponent.path = path
        imgComponent.url = URL.createObjectURL(path)

        setImg(imgComponent)
    }

    function AddIngredients() {
        setIngredients([...ingredients, ingredientsComponent])
    }

    function AddInstruction() {
        setInstructions([...instructions, instructionsComponent])
    }

    useEffect(() => {
        AddIngredients()
        AddInstruction()
    }, [])

    let showIngredientsDiv = ingredients.map(function(item, i) {
        return (
            <IngredientsDiv key={i} serialNum={i} ingredients={ingredients} setIngredients={setIngredients}/>
        )
    })

    let showInstructionsDiv = instructions.map(function(item, i) {
        return (
            <InstructionsDiv key={i} serialNum={i} instructions={instructions} setInstructions={setInstructions}/>
        )
    })

    async function AddToDatabase(img, name, description, time, servings, calories, carbs, protein, fat, veg, ingredients, instructions) {
        navigate('/')

        var ingredientsText
        var instructionsText

        let recipeForReviewId

        if (veg === 'true') {
            veg = true
        } else {
            veg = false
        }

        for(let i=0; i<ingredients.length; i++) {
            if(ingredients[i].name !== null && ingredients[i].quantity !== null) {
                ingredients[i] = `name=${ingredients[i].name} quantity=${ingredients[i].quantity}`
            }
        }

        for(let i=0; i<instructions.length; i++) {
            if(instructions[i].description !== null) {
                instructions[i] = `instructions=${instructions[i].description}`
            }
        }

        ingredientsText = ingredients.toString()
        instructionsText = instructions.toString()

        const {data, error} = await supabase
        .from('recipe')
        .insert({parentId: getCookie('userId'), name: name, description: description, veg: veg, time: time, servings: servings, calories: calories, carbs: carbs, protein: protein, fat: fat, ingredients: ingredientsText, instructions: instructionsText})
        .select('*')

        if (data) {
            console.log(data)
            recipeForReviewId = data[0].id
        }

        if (error) {
            console.log(error)
        }

        const {imgData, imgError} = await supabase
        .storage
        .from('images')
        .upload(`images/recipeImg/${recipeForReviewId}`, img.path)

        if(imgData) {
            console.log(imgData)
        }

        if(imgError) {
            console.log(imgError)
        }
    }

    const [trueOrFalse, setTrueOrFalse] = useState(null)
    
    function TrueOrFalseSelector() {
        if(trueOrFalse === true) {
            return (
                <div className="CRCNAICNADS_VegOrNonvegSection">
                    <button className="CRCNAICNADSVONS_Selected" onClick={() => {
                        setVeg('true')
                        setTrueOrFalse(true)}}>Veg</button>
                    <button className="CRCNAICNADSVONS_NotSelected" onClick={() => {
                        setVeg('false')
                        setTrueOrFalse(false)}}>Non-Veg</button>           
                </div>
            )
        } else if (trueOrFalse === false) {
            return (
                <div className="CRCNAICNADS_VegOrNonvegSection">
                    <button className="CRCNAICNADSVONS_NotSelected" onClick={() => {
                        setVeg('true')
                        setTrueOrFalse(true)}}>Veg</button>
                    <button className="CRCNAICNADSVONS_Selected" onClick={() => {
                        setVeg('false')
                        setTrueOrFalse(false)}}>Non-Veg</button>           
                </div>
            )
        } else {
            return (
                <div className="CRCNAICNADS_VegOrNonvegSection">
                    <button className="CRCNAICNADSVONS_NotSelected" onClick={() => {
                        setVeg('true')
                        setTrueOrFalse(true)}}>Veg</button>
                    <button className="CRCNAICNADSVONS_NotSelected" onClick={() => {
                        setVeg('false')
                        setTrueOrFalse(false)}}>Non-Veg</button>           
                </div>
            )
        }
    }

    const [NameRowHeight, setNameRowHeight] = useState(1)
    const [NameRowScrollHeight, setNameRowScrollHeight] = useState(null)

    const [DescriptionRowHeight, setDescriptionRowHeight] = useState(1)
    const [DescriptionRowScrollHeight, setDescriptionRowScrollHeight] = useState(null)

    return (
        <div className="CreateRecipeContainer">
            <div className="CRC_NameAndImgContianer">
                <div className="CRCNAIC_ImgSection">
                    <input className="CRCNAICIS_Img" type="image" src={img.url}/>
                    <input className="CRCNAICIS_BaseImg" type="file" onChange={(e) => {
                        AddImage(e.target.files[0])
                        }}/>
                </div>
                <div className="CRCNAIC_NameAndDescripSection">
                    <textarea 
                        rows={NameRowHeight}
                        onClick={(e) => {setNameRowScrollHeight(e.target.scrollHeight)}}
                        onInput={(e)=> {if(e.target.scrollHeight > NameRowScrollHeight) {
                                            setNameRowScrollHeight(e.target.scrollHeight)
                                            setNameRowHeight(NameRowHeight+1)
                                        }}} 
                        className="CRCNAIC_Name" type="text" placeholder="Name" onChange={(e) => {setName(e.target.value)}}/>
                    <textarea
                        rows={DescriptionRowHeight}
                        onClick={(e) => {setDescriptionRowScrollHeight(e.target.scrollHeight)}}
                        onInput={(e)=> {if(e.target.scrollHeight !== DescriptionRowScrollHeight) {
                                            setDescriptionRowScrollHeight(e.target.scrollHeight)
                                            setDescriptionRowHeight(DescriptionRowHeight+1)
                                        }}} 
                        className="CRCNAIC_Description" type="text" placeholder="Description" onChange={(e) => {setDescription(e.target.value)}}/>
                    <TrueOrFalseSelector/>

                    <div className="CRCNAIC_TimeAndServingsContainer">
                        <div className="CRCNAIC_TimeContainer">
                            <textarea rows={1} type="text" onChange={(e) => {setTime(e.target.value)}}/> min
                        </div>

                        <div className="CRCNAIC_ServingContainer">
                            <textarea  rows={1} type="text" onChange={(e) => {setServings(e.target.value)}}/> servings
                        </div>
                    </div>

                    <div className="CRCNAIC_NutrientsContainer">
                        <div className="CRCNAICNC_CaloriesDiv">
                            <div>Calories</div>
                            <div>
                                <textarea rows={1} type="text" onChange={(e) => {setCalories(e.target.value)}}/>
                            </div>
                        </div>
                        <div className="CRCNAICNC_VR"/>
                        <div className="CRCNAICNC_CarbsDiv">
                            <div>Carbs</div>
                            <div>
                                <textarea rows={1} type="text" onChange={(e) => {setCarbs(e.target.value)}}/>
                            </div>
                        </div>
                        <div className="CRCNAICNC_VR"/>
                        <div className="CRCNAICNC_ProteinDiv">
                            <div>Protein</div>
                            <div>
                                <textarea rows={1} type="text" onChange={(e) => {setProtein(e.target.value)}}/>
                            </div>
                        </div>
                        <div className="CRCNAICNC_VR"/>
                        <div className="CRCNAICNC_FatDiv">
                            <div>Fat</div>
                            <div>
                                <textarea rows={1} type="text" onChange={(e) => {setFat(e.target.value)}}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="CRC_IngAndInsContainer">
                <div className="CRC_InstructionsContainer">
                    <h2>Instructions</h2>
                    {showInstructionsDiv}
                    <button onClick={AddInstruction}><img src="img/AddBtn.svg"/>Add</button>
                </div>
                <div className="CRC_IngredientsContainer">
                    <h2>Ingredients</h2>
                    {showIngredientsDiv}
                    <button onClick={AddIngredients}><img src="img/AddBtn.svg"/>Add</button>
                </div>
            </div>

            <div className="CRC_SubmitBtn">
                <button onClick={() => {AddToDatabase(img, name, description, time, servings, calories, carbs, protein, fat, veg, ingredients, instructions)}}>
                    Submit
                </button>
            </div>
        </div>
    )
}

export default CreateRecipe