export function setCookie(signInStatus, data) {
    let date = new Date(Date.now() + 30*86400e3)
    let expires = date.toUTCString()

    let id = data.id
    let username = data.username
    let userPrivilege = data.userPrivilege
    let profilePhoto = data.profilePhoto

    document.cookie =  `userId=${id};expires=${expires}`
    document.cookie =  `username=${username};expires=${expires}`
    document.cookie =  `userPrivilege=${userPrivilege};expires=${expires}`
    document.cookie =  `profilePhoto=${profilePhoto};expires=${expires}`
    if(signInStatus === 'true') {
        document.cookie =  `signInStatus=true;expires=${expires}`
    }
}

export function getCookie(cname) {
    let cookie = document.cookie
    let cookieArr = cookie.split(';')
    for(let i=0; i<cookieArr.length; i++) {
        let cookieArrComp = cookieArr[i].split('=')
        if(` ${cname}` === cookieArrComp[0] || cname === cookieArrComp[0]) {
            return cookieArrComp[1]
        }
    }
}

export function deleteCookies() {
    var id = getCookie('userId')
    var userPrivilege = getCookie('userPrivilege')
    var username = getCookie('username')
    var profilePhoto = getCookie('profilePhoto')

    var expires = 'Sun, 01 Jan 2023 00:00:00 GMT'

    document.cookie =  `userId=${id};expires=${expires}`
    document.cookie =  `userPrivilege=${userPrivilege};expires=${expires}`
    document.cookie =  `signInStatus=true;expires=${expires}`
    document.cookie =  `username=${username};expires=${expires}`
    document.cookie =  `profilePhoto=${profilePhoto};expires=${expires}`
}

