import './App.css';

import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom"
import { useEffect, useState } from 'react';

import NavBar from './navbar';
import SignInPage from './pages/signInPages/signInPage';
import SignUpPage from './pages/signInPages/signupPage';
import Dashboard from './pages/dashboardPages/dashboard';
import Review from './pages/review/review';
import Recipe from './pages/recipe/recipe';
import HomePage from './pages/homePage';
import {getCookie} from './cookie/cookieManager';
import HomePages, { ReviewPages, UserPage } from './pages/createPages';
import supabase from './config/supabaseClient';
import CreateRecipe from './pages/dashboardPages/createRecipe';

function App() {
  const [signInStatus, setSignInStatus] = useState('false')
  const [recipes, setRecipes] = useState()

  useEffect(() => {
    async function getRecipes() {
      const {data, error} = await supabase
      .from('recipe')
      .select('*')

      if(data) {
        //console.log(data)
        setRecipes(data)
      }

      if(error) {
        console.log(error)
      }
    }

    getRecipes()

    if(getCookie('signInStatus') === 'true') {
      setSignInStatus('true')
    } else {
      setSignInStatus('false')
    }
  }, [])

  return (
    <BrowserRouter>
      <div className="App">
        <NavBar signInStatus={signInStatus} recipes={recipes}/>
        <HomePages/>
        <ReviewPages/>
        <Routes>
          <Route path='/' element={<Navigate to='/home'/>}/>
          <Route path='/home' element={<HomePage recipes={recipes}/>}/>
          <Route path='/create' element={<CreateRecipe/>}/>
          <Route path='/signin' element={<SignInPage setSignInStatus={setSignInStatus}/>}/>
          <Route path='/signup' element={<SignUpPage/>}/>
          <Route path='/dashboard' element={<Dashboard/>}/>
          <Route path='/recipe' element={<Recipe/>}/>
          <Route path='/review' element={<Review/>}/>
        </Routes>
      </div>
    </BrowserRouter>
  );
}

export default App;
