import { useEffect, useState } from "react"
import supabase from "../config/supabaseClient"

function SaveRecipe({userId, recipeId}) {
    const [savedRecipe, setSavedRecipe] = useState('')
    const [recipeToSave, setRecipeToSave] = useState('')

    function handleSave() {
        CheckIfAlreadySaved({userId, setSavedRecipe})
        RecipeToBeSaved({userId, recipeId, savedRecipe, setSavedRecipe, recipeToSave, setRecipeToSave})
    }

    useEffect(() => {
        // CheckIfAlreadySaved({userId, setSavedRecipe})
        // RecipeToBeSaved({userId, recipeId, savedRecipe, setSavedRecipe, recipeToSave, setRecipeToSave})
        handleSave()
    }, [])

    return (
        <button className="RE_SaveBtn" onClick={handleSave}>
            <img src="img/saveBtnIcon.svg"/>
        </button>
    )
}

