async function CheckIfAlreadySaved({userId, setSavedRecipe}) {
    const {data, error} = await supabase
    .from('User')
    .select('*')
    
    if (data) {
        //console.log(data)
    }

    if (error) {
        console.log(error)
    }

    for (var i=0; i<data.length; i++) {
        if(data[i].id === parseInt(userId)) {
            setSavedRecipe(data[i].savedRecipe)
        }
    }
}

export default SaveRecipe