import { useEffect, useState } from "react"
import supabase from "../config/supabaseClient" 
import { useNavigate } from "react-router-dom"
import SaveRecipe from "../components/saveRecipe"
import { getCookie } from "../cookie/cookieManager"

function HomePage({recipes}) {
    if (recipes) {
        return (
            <DisplayArray recipes={recipes}/>
        )
    }
}

function DisplayArray({recipes}) {
    const [dataComponent, setDataComponent] = useState([])

    useEffect(() => {
        setDataComponent(recipes)
    }, [])

    function getImg(userId) {
        const storage = supabase.storage.from('images')
        const getPublicUrl = storage.getPublicUrl(`images/recipeImg/${userId}`)
        return getPublicUrl.data.publicUrl
    }

    let showRecipeDiv = dataComponent.map(function(item, i) {
        return (
            <RecipeComponent key={i} id={item.id} img={getImg(item.id)} time={item.time} name={item.name} description={item.description} veg={item.veg} ingredients={item.ingredients} instructions={item.instructions}/>
        )
    })

    return (
        <div className="R_Array">
            {showRecipeDiv}
        </div>
    )
}

function RecipeComponent({id, img, time, name, description, veg, ingredients, instructions}) {  
    const navigate = useNavigate()

    function handleSave() {
        SaveRecipe(getCookie('userId'), id)
    }

    return (
        <div className="R_element">
            <div className="RE_ImgContainer">
                <img src={img}/>
                <span className="RE_TimeDisplayDiv">{time} min</span>
            </div>
            <div className="RE_NameAndBtnContainer">
                <div>{name}</div>
                <button className="RENABC_ShowRecipeBtn" onClick={() => {navigate(`/home/${name}`)}}>Show Recipe</button> 
                <SaveRecipe userId={getCookie('userId')} recipeId={id}/>
            </div>
        </div>
    )
}
   
export default HomePage